<template>
  <div class="UnionPay">
    <nav-head title="银联转账充值" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="padding-0-15">
      <div class="account-wrap">
        <div>
          <h2>官方账户</h2>
          <div>
            <span>开户行：</span>
            <span>{{ bank_name }}</span>
            <mt-button id="copy_1" class="btn-copy" size="small" plain type="primary" :data-clipboard-text="bank_name" @click="copy">复制</mt-button>
          </div>
          <div>
            <span>银行账户：</span>
            <span>{{bank_account}}</span>
            <mt-button id="copy_2" class="btn-copy" size="small" plain type="primary" :data-clipboard-text="bank_account" @click="copy">复制</mt-button>
          </div>
          <div>
            <span>账户名称：</span>
            <span>{{ account_name }}</span>
          </div>
        </div>
      </div>
      <div class="item-wrap">
        <div class="item">
          <span><b>*</b>付款人姓名</span>
          <input type="text" maxlength="20" v-model="name" @input="handleInputName" placeholder="请输入付款人姓名" v-bind:class="{'bank': name }"/>
          <span @click="clearValue('name')" class="icon-del-365" v-bind:class="{'show': name }"></span>
        </div>
        <div class="item">
          <span><b>*</b>付款金额</span>
          <input type="text" maxlength="9" v-model="money" @input="handleInputNum" placeholder="充值金额" v-bind:class="{'bank': money }"/>
          <span @click="clearValue('money')" class="icon-del-365" v-bind:class="{'show': money }"></span>
        </div>
        <div class="item">
          <span><b style="opacity: 0;">*</b>付款说明</span>
          <input type="text" maxlength="50" v-model="remark" placeholder="备注信息，选填" v-bind:class="{'bank': remark }"/>
          <span @click="clearValue('remark')" class="icon-del-365" v-bind:class="{'show': remark }"></span>
        </div>
      </div>
      <div class="item-wrap">
        <p>完成转账后，客服会主动与您联系！</p>
        <p>注：转账完成后请提交正确的金额，充值人姓名（付款银行卡的实名制姓名）</p>
      </div>
      <mt-button size="large" v-bind:disabled="!money || !name" @click.native="bindBank" class="btn-bind" v-bind:class="{'available': money && name }">发送充值信息</mt-button>
    </div>
  </div>
</template>

<script>//
import NavHead from '@/components/NavHead.vue'
import { Button, Toast } from 'mint-ui'
import Clipboard from 'clipboard'
import { mapActions } from 'vuex'
import { ADD_PAYMENT_MSG } from '../../../vuex/Mine'

export default {
  name: 'rechargeByUnionPay',
  data () {
    return {
      name: '',
      money: '',
      remark: '',
      bank_name: this.$route.params.item.bank_name,
      bank_account: this.$route.params.item.bank_account,
      account_name: this.$route.params.item.account_name,
      CopySuccessMessage: ['开户行信息已复制', '银行账户信息已复制']
    }
  },
  methods: {
    bindBank () {
      if (!this.name) {
        Toast('请输入真实姓名!')
        return
      }
      if (!this.money) {
        Toast('请输入付款金额!')
        return
      }
      console.log(this.name + ' ' + this.money)
      this.addPaymentMsg({
        channel_id: 5,
        payer_name: this.name,
        payer_money: this.money,
        payer_remark: this.remark
      })
    },
    handleInputNum (e) {
      this.money = e.target.value.replace(/[^\d]/g, '')
    },
    handleInputName (e) {
      this.name = e.target.value.replace(/[^a-zA-Z0-9\u4E00-\u9FA5]/g, '')
    },
    clearValue (v) {
      this[v] = ''
    },
    copy () {
      let clipboard = new Clipboard('.btn-copy')
      clipboard.on('success', e => {
        Toast(this.CopySuccessMessage[e.trigger.id.split('_')[1] - 1])
        clipboard.destroy()
      })
      clipboard.on('error', e => {
        Toast('复制失败')
        clipboard.destroy()
      })
    },
    ...mapActions({
      addPaymentMsg: ADD_PAYMENT_MSG
    })
  },
  components: {
    NavHead,
    [Button.name]: Button,
    Clipboard
  }
}
</script>
<style scoped lang="scss">
  .account-wrap {
    margin-top: 18px;
    > div {
      background-image: url("../../../assets/mine/bg-recharge@2x.png");
      background-size: 100% 100%;
      border-radius: 5px;
      padding: 12px 15px;
      > h2 {
        font-size: 20px;
        color: #FEFEFE;
        text-align: center;
        padding-bottom: 12px;
      }
      > div {
        color: #FEFEFE;
        font-size: 14px;
        line-height: 24px;
        overflow: hidden;
        padding-bottom: 8px;
        > span:nth-child(1) {
          float: left;
        }
        > span:nth-child(2) {
          float: left;
          width: calc(100% - 122px);
        }
        > .btn-copy {
          border: none;
          float: right;
          background-color: #999;
          color: white;
          font-size: 12px;
          height: 24px;
          line-height: 24px;
          padding: 0 13px;
          border-radius: 5px;
        }
      }
      > div:last-child {
        padding-bottom: 0;
      }
    }
  }
  .item-wrap {
    background-color: $background-light-color;
    padding: 0 15px;
    border-radius: 5px;
    margin-top: 18px;
    .item {
      height: 54px;
      line-height: 54px;
      text-align: left;
      font-size: 17px;
      position: relative;
      overflow: hidden;
      border-bottom: 1px solid $border-color;
      span:first-child {
        color: $font-color;
        b {
          color: #FF3333;
          font-size: 17px;
        }
      }
      input {
        float: right;
        border: none;
        outline: none;
        display: inline-block;
        width: 75%;
        position: absolute;
        right: 0;
        top: 0;
        padding: 0 0;
        font-size: 15px;
        height: 54px;
        line-height: 54px;
        color: #CCC;
        background: transparent;
        border-radius: 0;
        text-align: right;
      }
      input.bank {
        margin-right: 30px;
      }
      .icon-del-365 {
        position: absolute;
        right: 0;
        top: 15px;
        display: none;
      }
      .icon-del-365.show {
        display: inline-block;
      }
    }
    .item:last-child {
      border-bottom: none;
    }
    > p:first-child {
      color: $back-color;
      font-size: 14px;
      font-weight: bold;
      text-align: center;
      padding: 10px 0;
    }
    > p:last-child {
      color: #CCC;
      font-size: 12px;
      line-height: 22px;
      padding-bottom: 10px;
    }
  }

  .btn-bind {
    border: none;
    background-color: #ccc;
    color: white;
    box-shadow: none;
    height: 48px;
    font-size: 17px;
    margin-top: 40px;
    margin-bottom: 20px;
  }
  .btn-bind.available{
    background-color: $back-color;
    @if $IF365 { color: #131313; }
  }
  .padding-0-15 {
    padding: 0 15px;
  }
</style>
